    getContractList(params, tenantId) {
        const url = baseUrl + `api/v1/contract/company/${tenantId}/contracts`
        return axios({
            method: 'get',
            url: url,
            params
        }).then((res) => {
            return Promise.resolve(res.data)
        }).catch(function (error) {

        });
    },
