<template>
  <div id="customerManage">
    <div>
      <p class="overview_title">
        <i></i>
        <span></span>
      </p>
      <div class="check_content">
        <div
          class="check-item"
          v-for="(item,index) in overviewListOne"
          :key="index"
          @click="selectRecordList(item.value)"
          :style="item.value==selectRecord?'color:#256fff':'color:#2c3e50'"
        >
          <div class="overview-icon">
            <img
              :src="item.status==1?yishou:item.status==2?yingshou:item.status==3?yifu:item.status==4?yingfu:''"
              alt
            />
          </div>
          <div class="overview-content">
            <span>{{item.status==1?staticInfo.receivedNum:item.status==2?staticInfo.receivableNum:item.status==3?staticInfo.paidNum:item.status==4?staticInfo.payableNum:0}}</span>
            <p>{{item.status==1?'':item.status==2?'':item.status==3?'':item.status==4?'':''}}</p>
          </div>
        </div>
      </div>
      <div class="check_content" style="margin-top:12px;">
        <div
          class="check-item"
          v-for="(item,index) in overviewList"
          :key="index"
          @click="selectRecordList(item.value)"
          :style="item.value==selectRecord?'color:#256fff':'color:#2c3e50'"
        >
          <div class="overview-icon">
            <img
              :src="item.status==1?daiqianyue:item.status==2?jijiangdaoqi:item.status==3?xujiaozujin:item.status==4?xutui:''"
              alt
            />
          </div>
          <div class="overview-content">
            <span>{{item.status==1?staticInfo.unsignedNum:item.status==2?staticInfo.overdueNum:item.status==3?staticInfo.payableRentNum:item.status==4?staticInfo.payableRefundNum:0}}</span>
            <p>{{item.status==1?'':item.status==2?'':item.status==3?'':item.status==4?'':''}}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="nav">
      <div class="overview_title">
        <i></i>
        <span></span>
      </div>
      <!-- <span style="font-size:12px;"></span> -->
      <div class="operate_button">
        <el-button type="primary" size="mini" @click="navigatePage('addSignReceipts','add')"></el-button>
        <el-button type="primary" size="mini" @click="navigatePage('rentCollection','add')"></el-button>
        <el-button type="primary" size="mini" @click="navigatePage('addAccepted','add')"></el-button>
        <el-button type="primary" size="mini" @click="navigatePage('addReceivable','add')"></el-button>
        <!-- <el-button type="primary" size="mini" @click="navigatePage('addCustomer','add')"></el-button> -->
        <el-button type="primary" size="mini" @click="navigatePage('addPaid','add')"></el-button>
        <el-button type="primary" size="mini" @click="navigatePage('addPayable','add')"></el-button>
        <el-button type="primary" size="mini" @click="navigatePage('returnDeposit','add')"></el-button>
      </div>
    </div>
    <div class="customer_list">
      <div class="search">
        <div>
          <span style="font-size:14px;"></span>
          <el-select
            v-model="searchInfo.recordType"
            placeholder=""
            size="mini"
            @change="selectType"
            :disabled="selectRecord=='unsignCustomer'||selectRecord=='beOverdueCustomer'||selectRecord=='rentPayableRecord'||selectRecord=='depositRequired'?true:false"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.lable"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-input
            placeholder=""
            v-model="searchInfo.searchContent"
            size="mini"
            :disabled="selectRecord=='unsignCustomer'||selectRecord=='beOverdueCustomer'||selectRecord=='rentPayableRecord'||selectRecord=='depositRequired'?true:false"
          ></el-input>
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-search"
            @click="selectType"
            :disabled="selectRecord=='unsignCustomer'||selectRecord=='beOverdueCustomer'||selectRecord=='rentPayableRecord'||selectRecord=='depositRequired'?true:false"
          ></el-button>
        </div>
        <el-button size="mini" type="primary" icon="el-icon-search"></el-button>
      </div>
      <!-- *********************  *****************88 -->
      <div v-show="selectRecord=='receivedList'">
        <div class="search">
          <p style="font-size:14px"></p>
        </div>
        <el-table
          :data="customerList"
          style="width: 98%"
          :header-cell-style="{'background':'#f1f4fb','text-align':'center','font-size':'14px','color':'#222','font-weight':'normal'}"
          :cell-style="{'text-align':'center','font-size':'14px','color':'#222','font-weight':'normal'}"
        >
          <el-table-column label="" width="70">
            <template slot-scope="scope">
              <span>{{indexMethods(scope.$index)}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="userName" label=""></el-table-column>
          <el-table-column prop="arDate" label=""></el-table-column>
          <el-table-column prop="arAmount" label=""></el-table-column>
          <el-table-column prop="paidDate" label=""></el-table-column>
          <el-table-column prop="paidAmount" label=""></el-table-column>
          <el-table-column prop="licensePlate" label=""></el-table-column>
          <el-table-column prop="nextArDate" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scoped">
              <span>{{scoped.row.recordType==0?'':scoped.row.recordType==1?'':scoped.row.recordType==2?'':scoped.row.recordType==3?'':scoped.row.recordType==4?'':scoped.row.recordType==5?'':scoped.row.recordType==6?'':scoped.row.recordType==7?'':scoped.row.recordType==8?'':scoped.row.recordType==9?'':scoped.row.recordType==10?'':scoped.row.recordType==11?'':scoped.row.recordType==12?'':''}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="remarks" label=""></el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span
                style="font-size:14px;color:#1890ff;cursor:pointer"
                @click="gotoDetail(scope.row)"
              ></span>
              <i class="splitLine"></i>
              <span
                style="font-size:14px;color:#e53935;cursor:pointer"
                @click="deleteCustomer(scope.row)"
              ></span>
            </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <span>{{total}}</span>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="size"
            layout=" prev, pager, next, sizes,jumper"
            :total="total"
            background
          ></el-pagination>
        </div>
      </div>
      <!-- *************** ************************ -->
      <keep-alive>
        <component
          v-show="selectRecord!==`receivedList`"
          :is="selectView"
          :searchInfo="searchInfo"
          ref="isComponent"
        ></component>
      </keep-alive>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "@/api/contractApi";
import daiqianyue from "../../assets/images/caiwu/ic_caiwu_daiqianyue.svg";
import jijiangdaoqi from "../../assets/images/caiwu/ic_caiwu_yuqi.svg";
import xujiaozujin from "../../assets/images/caiwu/ic_caiwu_xujiaozujin.svg";
import xutui from "../../assets/images/caiwu/ic_caiwu_xutuiyajin.svg";
import yishou from "../../assets/images/caiwu/ic_caiwu_yishoukuan.svg";
import yingshou from "../../assets/images/caiwu/ic_caiwu_yingshoukuan.svg";
import yifu from "../../assets/images/caiwu/ic_caiwu_yifukuan.svg";
import yingfu from "../../assets/images/caiwu/ic_caiwu_yingfukuan.svg";
import receivableRecord from "@/components/financeManage/receivableRecord";
import payableRecord from "@/components/financeManage/payableRecord";
import unsignCustomer from "@/components/financeManage/unsignCustomer";
import paidRecord from "@/components/financeManage/paidRecord";
import beOverdueCustomer from "@/components/financeManage/beOverdueCustomer";
import rentPayableRecord from "@/components/financeManage/rentPayableRecord";
import depositRequired from "@/components/financeManage/depositRequired";
export default {
  name: `customerManage`,
  data() {
    return {
      customerList: [],
      daiqianyue,
      jijiangdaoqi,
      xujiaozujin,
      xutui,
      page: 1,
      size: 10,
      yishou,
      yingshou,
      yifu,
      yingfu,
      search: ``,
      selectRecord: "receivedList",
      selectStatusValue: "receivedList",
      receivableRecord,
      payableRecord,
      unsignCustomer,
      beOverdueCustomer,
      paidRecord,
      rentPayableRecord,
      depositRequired,
      searchInfo: {},
      total: 0,
      staticInfo:{},
      typeList: [
        { value: "", lable: "" },
        { value: 0, lable: "" },
        { value: 1, lable: "" },
        { value: 2, lable: "" },
        { value: 3, lable: "" },
        { value: 4, lable: "" },
        { value: 5, lable: "" },
        { value: 6, lable: "" },
        { value: 7, lable: "" },
        { value: 8, lable: "" },
        { value: 9, lable: "" },
        { value: 10, lable: "" },
        { value: 11, lable: "" },
        { value: 12, lable: "" }
      ],
      overviewList: [
        { id: 1, status: 1, value: "unsignCustomer" },
        { id: 2, status: 2, value: "beOverdueCustomer" },
        { id: 3, status: 3, value: "rentPayableRecord" },
        { id: 4, status: 4, value: "depositRequired" }
      ],
      overviewListOne: [
        { id: 1, status: 1, value: "receivedList" },
        { id: 2, status: 2, value: "receivableRecord" },
        { id: 3, status: 3, value: "paidRecord" },
        { id: 4, status: 4, value: "payableRecord" }
      ]
    };
  },
  activated() {
    // this.getuser()
    this.getAcceptedList();
    this.getStatics();
  },
  computed: {
    ...mapState(`user`, [`access_token`, `tenantId`]),
    selectView() {
      return this.selectRecord == "receivableRecord"
        ? "receivableRecord"
        : this.selectRecord == "payableRecord"
        ? "payableRecord"
        : this.selectRecord == "unsignCustomer"
        ? "unsignCustomer"
        : this.selectRecord == "paidRecord"
        ? "paidRecord"
        : this.selectRecord == "beOverdueCustomer"
        ? "beOverdueCustomer"
        : this.selectRecord == "rentPayableRecord"
        ? "rentPayableRecord"
        : this.selectRecord == "depositRequired"
        ? "depositRequired"
        : "";
    }
  },
  components: {
    receivableRecord,
    payableRecord,
    unsignCustomer,
    paidRecord,
    beOverdueCustomer,
    rentPayableRecord,
    depositRequired
  },
  methods: {
    handleSizeChange(val) {
      console.log(`${val}`);
      this.size = val;
      this.getAcceptedList();
    },
    handleCurrentChange(num) {
      console.log(`: ${num}`);
      this.page = num;
      this.getAcceptedList();
    },
    getStatics(){
      let tenantId = this.tenantId
      http.getFinanceStatics(tenantId).then(data=>{
        if(data.code==0){
          this.staticInfo = data.data
        }
      })
    },
    navigatePage(page, pageType) {
      console.log(pageType);
      console.log(page);
      this.$router.push({ name: page, query: { pageType } });
    },
    selectType(value) {
      console.log(value);
      if (this.selectRecord == "receivedList") {
        this.getAcceptedList();
      } else {
        this.$refs.isComponent.getMotRecord();
      }
    },
    gotoDetail(row) {
      this.$router.push({ name: `addAccepted`, query: { pageType: "detail", } });
    },
    indexMethods(index) {
      // return (this.activeForm.page - 1) * this.activeForm.size + index + 1;
    },
    getAcceptedList() {
      let { page, size } = this;
      let tenantId = this.tenantId;
      let params = this.searchInfo;
      // let params = {recordType};
      http.getAccepted(params, tenantId, page, size).then(data => {
        if (data.code == 0) {
          this.customerList = data.data;
          this.total = data.total;
        }
      });
    },
    // 
    deleteCustomer(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    selectRecordList(value) {
      console.log(value);
      this.selectRecord = value;
      this.searchInfo = {};
    }
  }
};
</script>
<style lang="stylus" scoped>
#customerManage {
  margin: 12px;

  .check_content {
    // padding: 20px;
    background: #fff;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .check-item {
    // flex: 1;
    display: flex;
    align-items: center;
    padding: 20px 0 20px 20px;
    cursor: pointer;

    &:hover {
      // background:red
      color: #1890ff;
    }

    span {
      font-size: 12px;
    }

    p {
      font-size: 16px;
      font-weight: bold;
      padding: 0;
      margin: 0;
      margin-top: 18px;
    }

    &:nth-last-child(1) {
      // border: 0;
      .overview-content {
        border: 0;
      }
    }
  }

  .nav {
    margin-top: 8px;
    // padding: 12px;
    background: #fff;
  }

  .customer_list {
    background: #fff;
    margin-top: 12px;
    // padding-bottom: 12px;
  }

  .el-button--primary {
    color: #FFF;
    background-color: #1890ff;
    border-color: #1890ff;
  }

  .search {
    display: flex;
    align-items: center;
    padding: 8px 12px 8px 12px;
    border-bottom: 1px solid #eee;
    justify-content: space-between;
    // width 100%;
    background: #fff;
  }

  .el-input {
    width: 220px;
    margin: 0 12px;
  }

  .el-table {
    margin: 0 auto;
    width: 98%;
    padding-top: 12px;
  }

  .el-pagination {
    // margin-top: 12px;
    // padding-left: 12px;
  }

  .overview_title {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #dcdfe6;
    padding: 10px;
    margin: 0;
    background: #fff;

    i {
      display: inline-block;
      width: 2px;
      height: 16px;
      background: #256fff;
      margin-right: 12px;
    }

    span {
      font-size: 16px;
    }
  }

  .overview-icon {
    width: 59px;
    height: 59px;
    border-radius: 50px;
    margin-right: 15px;
  }

  .overview-content {
    display: flex;
    align-items: center;
    flex-direction: column;
    border-right: 1px solid #dcdfe6;
    padding-right: 20px;

    span {
      font-size: 28px;
    }

    p {
      font-size: 14px;
    }
  }

  .operate_button {
    padding: 20px;
  }

  .pagination {
    // width: 100%
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 40px;

    span {
      font-size: 12px;
    }
  }

  .splitLine {
    display: inline-block;
    height: 14px;
    width: 1px;
    background: #e4e7ed;
    margin: 0 12px;
  }
}
</style>


