<template>
  <div id="addCustomer">
    <div style="background:#fff">
      <div class="add_title">
        <el-button size="mini" type="primary" icon="el-icon-back" @click="$router.go(-1)"></el-button>
        <p>{{pageType=='add'?'':''}}</p>
      </div>
    </div>
    <div class="contractInfo" v-if="contractInfo.customer">
      <p></p>
      <div class="contract_detail">
        <div class="contract_detail_one">
          <p>{{contractInfo.leaseType=="MONTH"?"":''}}</p>
          <p v-if="contractInfo.customer">{{contractInfo.customer.customerName}}</p>
          <p v-if="contractInfo.customer">{{contractInfo.customer.customerPhone}}</p>
          <p v-if="contractInfo.customer">{{contractInfo.customer.customerIdCardNo}}</p>
          <p>{{contractInfo.contractNo}}</p>
          <p v-if="contractInfo.leaseType=='MONTH'">{{contractInfo.months}}</p>
          <p>{{contractInfo.getTime}}</p>
        </div>
        <div class="contract_detail_one">
          <p></p>
          <el-table :data="contractInfo.motors" style="width: 80%" height="210">
            <el-table-column prop="plateNumber" label=""></el-table-column>
            <el-table-column prop="brandModel" label=""></el-table-column>
            <!-- <el-table-column prop="address" label=""></el-table-column> -->
          </el-table>
        </div>
        <div class="contract_detail_one">
          <p>{{contractInfo.startTime}}</p>
          <p>{{contractInfo.endTime}}</p>

          <p>{{contractInfo.deposit}}</p>
          <p>{{contractInfo.rent}}</p>
          <p>{{contractInfo.managementFee}}</p>
          <p>{{contractInfo.basicPremium}}</p>
          <p>{{contractInfo.aer}}</p>
          <p v-if="contractInfo.leaseType=='DAY'">{{contractInfo.leaseTerm}}</p>
        </div>
      </div>
    </div>
    <div class="addForm">
      <el-row :gutter="60">
        <el-col :span="12" :xl="8">
          <!-- <div class="grid-content bg-purple"></div> -->
          <el-form
            key="carForm1"
            :model="acceptedInfo"
            :rules="rules"
            label-position="right"
            class="demo-form-inline"
            ref="acceptedInfo"
            label-width="auto"
          >
            <el-form-item label="" prop="recordType">
              <el-select
                v-model="acceptedInfo.recordType"
                size="small"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
              >
                <el-option
                  v-for="item in typeList"
                  :label="item.lable"
                  :value="item.value"
                  :key="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="licensePlate">
              <el-autocomplete
                v-model="acceptedInfo.licensePlate"
                :fetch-suggestions="querySearchAsync"
                placeholder=""
                size="mini"
                @select="handleSelect"
                :disabled="pageType=='detail'?true:false"
              ></el-autocomplete>
            </el-form-item>
            <el-form-item label="" prop="brand">
              <el-input
                v-model="acceptedInfo.brand"
                class="brand_input"
                size="mini"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="model">
              <el-input
                v-model="acceptedInfo.model"
                class="brand_input"
                size="mini"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="userName">
              <el-input
                v-model="acceptedInfo.userName"
                class="brand_input"
                size="mini"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="payRole">
              <el-radio v-model="acceptedInfo.payRole" label="0"></el-radio>
              <el-radio v-model="acceptedInfo.payRole" label="1"></el-radio>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <!-- <div class="grid-content bg-purple"></div> -->
          <el-form
            key="carForm2"
            :model="acceptedInfo"
            label-position="right"
            class="demo-form-inline"
            ref="acceptedInfo"
            :rules="rules"
            label-width="auto"
          >
            <el-form-item label="">
              <el-input
                v-model="acceptedInfo.contractNo"
                size="mini"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="paidDate">
              <el-date-picker
                size="mini"
                v-model="acceptedInfo.paidDate"
                type="date"
                placeholder=""
                value-format="yyyy-MM-dd"
                :disabled="pageType=='detail'?true:false"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="paidAmount">
              <el-input
                v-model="acceptedInfo.paidAmount"
                size="mini"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
              ></el-input>
            </el-form-item>

            <el-form-item label="">
              <el-select
                v-model="acceptedInfo.payer"
                size="small"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
                @change="selectPay"
              >
                <el-option
                  v-for="item in staffList"
                  :key="item.memberId"
                  :label="item.name"
                  :value="item.memberId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input
                type="textarea"
                v-model="acceptedInfo.remarks"
                rows="5"
                size="mini"
                placeholder=""
                :disabled="pageType=='detail'?true:false"
              ></el-input>
            </el-form-item>
            <el-form-item style="margin-top:30px;" v-if="pageType=='add'">
              <el-button size="mini" type="primary" @click="addMot"></el-button>
              <el-button size="mini" type="primary" @click="clear"></el-button>
            </el-form-item>
            <el-form-item style="margin-top:30px;" v-else>
              <el-button
                size="small"
                type="primary"
                @click="editMot"
              >{{pageType=='detail'?'':''}}</el-button>
              <!--        -->
              <el-button size="small" type="primary" @click="deleteCar"></el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
    <div class="checkForm">
      <p class="checkInfo"></p>
      <el-form
        key="carForm3"
        :model="acceptedInfo"
        label-position="right"
        class="demo-form-inline"
        label-width="auto"
      >
        <div v-for="(item,index) in acceptedInfo.addKeys" :key="index" style="margin:24px;">
          <el-form-item label="">
            <!-- TODO  -->
            <el-upload
              class="avatar-uploader"
              :action="actions"
              :data="formData"
              :show-file-list="false"
              :on-success="handleDravelSuccess"
              :before-upload="beforeDravelUpload"
              :on-error="uploadError"
              v-loading="uploadLoading"
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8)"
            >
              <img v-if="item.url" :src="item.url" class="avatar" @click="curRowIndex=index" />
              <p v-else class="avatar-uploader-icon" @click="curRowIndex=index">
                <i class="el-icon-plus"></i>
                <span></span>
              </p>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <el-input
              type="textarea"
              v-model="item.picDesc"
              rows="5"
              size="mini"
              placeholder=""
              :disabled="pageType=='detail'?true:false"
            ></el-input>
          </el-form-item>
        </div>
        <div class="addCheckInfo">
          <el-button type="primary" @click="addCheckInfo"></el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import http1 from "@/api/riskManagementApi";
import http from "@/api/carApi";
import signHttp from "@/api/contractApi";
import deployHttp from "@/api/deployApi";

export default {
  data() {
    return {
      acceptedInfo: {
        brand: "",
        model: "",
        payRole: "0",
        addKeys: [],
        userName: "",
        contractNo: "",
        licensePlate: ""
      },
      actions: `http://upload-z1.qiniup.com`,
      formData: { token: "" },
      curRowIndex: "",
      uploadLoading: false,
      staffList: [],
      contractInfo: {},
      imageUrl: "",
      brandModel: "",
      rules: {
        licensePlate: [
          { required: true, message: "", trigger: "blur" }
        ],
        recordType: [
          { required: true, message: "", trigger: "change" }
        ],
        userName: [
          { required: true, message: "", trigger: "blur" }
        ],
        brand: [{ required: true, message: "", trigger: "blur" }],
        model: [{ required: true, message: "", trigger: "blur" }],
        paidDate: [
          { required: true, message: "", trigger: "change" }
        ],
        paidAmount: [
          { required: true, message: "", trigger: "blur" }
        ],
        payRole: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      typeList: [
        { value: 0, lable: "" },
        { value: 1, lable: "" },
        { value: 2, lable: "" },
        { value: 3, lable: "" },
        { value: 4, lable: "" },
        { value: 5, lable: "" },
        { value: 6, lable: "" },
        { value: 7, lable: "" },
        { value: 8, lable: "" },
        { value: 9, lable: "" },
        { value: 10, lable: "" },
        { value: 11, lable: "" },
        { value: 12, lable: "" }
      ],
      pageType: "add",
      pageStatus: 1, //0  1
      diffData: null,
      newMotInfo: {
        addKeys: []
      },
      examinePics: []
    };
  },
  computed: {
    ...mapState(`user`, [`access_token`, `tenantId`])
  },
  activated() {
    console.log(this.$route.query.pageType);
    this.pageType = this.$route.query.pageType;
    this.pageStatus = 1;
    this.pricePageStatus = 1;
    this.acceptedInfo = {
      brand: "",
      model: "",
      payRole: "0",
      addKeys: [],
      userName: "",
      contractNo: "",
      licensePlate: ""
    };
    if (this.$route.query.recordInfo) {
      let recordInfo = JSON.parse(this.$route.query.recordInfo);
      let delKeys = [];
      let addKeys = [];
      recordInfo.pictures.forEach(item => {
        let info = {
          picKey: item.picKey,
          picDesc: item.picDesc,
          id: item.id,
          url: item.picPath
        };
        delKeys.push(item.id);
        addKeys.push(info);
      });
      console.log(delKeys);
      this.examinePics = recordInfo.pictures;
      this.acceptedInfo = {
        id: recordInfo.id, //
        licensePlate: recordInfo.licensePlate, //
        contractNo: recordInfo.contractNo, //
        handlerId: recordInfo.handlerId, //
        handlerName: recordInfo.handlerName, //
        examineTime: recordInfo.examineTime, //
        examineType: recordInfo.examineType, //0-1-2-
        vehicleLicense: recordInfo.vehicleLicense + "", //0-1-
        operatePermit: recordInfo.operatePermit + "", //0-1-
        mileage: recordInfo.mileage, //0
        remarks: recordInfo.remarks, //
        addKeys: addKeys, //key
        delKeys: [] //
      };

      let cate = JSON.stringify(this.acceptedInfo);
      this.newMotInfo = JSON.parse(cate);
    }
    this.getStaffList();
  },
  methods: {
    //   
    beforeDravelUpload(file) {
      this.uploadLoading = true;
      console.log(file);
      let name = file.name;
      let params = { fileName: name };
      let _this = this;
      if (file.type.indexOf("image") < 0) {
        this.$message(``);
        this.uploadLoading = false;
        return false;
      }
      return deployHttp.getUploadToken(params).then(data => {
        console.log(data.data.token);
        _this.formData.token = data.data.token;

        this.acceptedInfo.addKeys[this.curRowIndex].picKey = data.data.key;
      });
    },

    // 
    handleDravelSuccess(res) {
      console.log(res);
      this.uploadLoading = false;
      this.$message(``);
      // this.imageUrl.push({ url: res.payload.url });
      this.acceptedInfo.addKeys[this.curRowIndex].url = res.payload.url;
    },
    //
    uploadError(error) {
      console.log(error);
      this.$message(``);
      this.uploadLoading = false;
    },
    getStaffList() {
      // TODO   
      let params = { page: 1, size: 20 };
      let qArr = ["com_admin", "com_accountant"];
      let tenantId = this.tenantId;
      deployHttp.getStaffList(params, tenantId, qArr).then(data => {
        console.log(data);
        if (data.code == 0) {
          this.staffList = data.data;
          // this.total = data.total;
        } else {
          this.$message(data.desc);
        }
      });
    },
    selectPay(value) {
      console.log(value);
      let roleInfo = this.staffList.find(item => {
        // console.log(item)
        return item.memberId == value;
      });
      this.acceptedInfo.payer = roleInfo.name;
    },
    addCheckInfo() {
      let info = { picKey: "", picDesc: "" };
      this.acceptedInfo.addKeys.push(info);
    },
    //
    addMot() {
      let { acceptedInfo, tenantId } = this;
      acceptedInfo.addKeys = acceptedInfo.addKeys.filter(item => {
        return item.picKey != "" || item.picDesc != "";
      });
      let arr = [];
      acceptedInfo.addKeys.forEach(item => {
        return arr.push({ picKey: item.picKey, picDesc: item.picDesc });
      });
      acceptedInfo.addKeys = arr;
      console.log(acceptedInfo.addKeys);
      console.log(acceptedInfo);
      this.$refs.acceptedInfo.validate(valid => {
        if (valid) {
          signHttp.addAccepted(acceptedInfo, tenantId).then(data => {
            console.log(data);
            if (data.code == 0) {
              this.$message(``);
              this.$router.back(-1);
            } else {
              this.$message(data.desc);
            }
          });
        } else {
          this.$message(``);
        }
      });
    },
    //
    querySearchAsync(queryString, cb) {
      let tenantId = this.tenantId;
      let plateNumber = queryString;
      let status = ["SIGNED", "FINISH", "TERMINATED_IN_ADVANCE"];
      if (!queryString) {
        return;
      }
      let addVehicleList = [{}];
      signHttp.getContractCar(tenantId, plateNumber, status).then(data => {
        console.log(data.data);
        addVehicleList = [{ value: "" }];
        data.data.forEach(item => {
          let obj = {
            value: item.plateNumber + "=>" + item.customerName,
            id: item.contractId,
            licensePlate: item.plateNumber,
            customerName: item.customerName
          };
          addVehicleList.push(obj);
        });
        cb(addVehicleList);
      });
    },
    handleSelect(item) {
      console.log(item);
      let tenantId = this.tenantId;
      let licensePlate = item.licensePlate;
      let contractId = item.id;
      this.acceptedInfo.licensePlate = licensePlate;
      this.acceptedInfo.userName = item.customerName;
      this.acceptedInfo.contractId = item.id;
      let that = this;
      http1.getVehicleModel({ licensePlate }, tenantId).then(data => {
        console.log(data);
        if (data.code == 0) {
          that.brandModel = data.data.brand + "-" + data.data.model;
          that.acceptedInfo.brand = data.data.brand;
          that.acceptedInfo.model = data.data.model;
          //   that.acceptedInfo.model = data.data.model;
        }
      });
      signHttp.getContractDetail(tenantId, contractId).then(data => {
        console.log(data.data);
        if (data.code == 0) {
          this.contractInfo = data.data;
          this.acceptedInfo.contractNo = data.data.contractNo
            ? data.data.contractNo
            : "";
        }
      });
    },
    clear() {
      this.acceptedInfo = {};
    },
    //
    deleteCar(row) {
      let params = { id: acceptedInfo.id };
      let tenantId = this.tenantId;
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          http.deleteCheckRecord(params, tenantId).then(data => {
            if (data.code == 0) {
              this.$message(``);
              this.$router.back(-1);
            } else {
              this.$message(data.desc);
            }
          });
        })
        .catch(() => {});
    },
    //
    diffDevinfo() {
      for (let k in this.newMotInfo) {
        let delKeys = [];
        if (this.newMotInfo[k] != this.acceptedInfo[k]) {
          if (!this.diffData) {
            this.diffData = {};
          }
          if (this.newMotInfo["addKeys"] != this.acceptedInfo["addKeys"]) {
            this.acceptedInfo.addKeys.filter(item => {
              this.acceptedInfo.delKeys.push(item.id);
            });
          }
          this.diffData[k] = this.acceptedInfo[k];
          // this.diffData['licensePlate'] = this.licensePlate
        }
      }
    },

    //
    editMot() {
      console.log(`aaa`);
      this.pageStatus = this.pageStatus == 0 ? 1 : 0;
      let { acceptedInfo, tenantId } = this;

      let that = this;

      // this.pageType = this.pageStatus == 0 ? "detail" : "edit";
      if (this.pageStatus == 1) {
        this.pageType = "detail";
        this.diffDevinfo();
        console.log(this.diffData);
        if (!this.diffData) {
          that.$message(``);
          return;
        } else {
          this.diffData.id = this.acceptedInfo.id;
        }
        let params = !this.diffData ? acceptedInfo : this.diffData;
        let arr = [];
        params.addKeys.filter(item => {
          arr.push({ picDesc: item.picDesc, picKey: item.picKey });
        });
        params.addKeys = arr;
        params.addKeys = params.addKeys.filter(item => {
          return item.picKey != "" || item.picDesc != "";
        });
        // params.licensePlate = this.licensePlate
        // priceForm.licensePlate = this.licensePlate;
        http.editCheckRecord(params, tenantId).then(data => {
          if (data.code == 0) {
            that.$message(``);
            that.$router.back(-1);
          } else {
            that.$message(data.desc);
          }
        });

        //
      } else {
        this.pageType = "edit";
      }
    }
  }
};
</script>
<style lang="stylus">
#addCustomer {
  margin: 0 24px;

  .add_title {
    padding: 12px 20px;
    border-bottom: 1px solid #eaeaea;
    font-size: 14px;
    display: flex;
    align-items: center;

    p {
      font-size: 16px;
      margin-left: 20px;
    }
  }

  .contractInfo {
    margin-top: 12px;
    background: #fff;
    // padding:24px;
    padding: 12px;

    p {
      padding: 0;
      margin: 0;
      font-size: 16px;
    }
  }

  .contract_detail {
    display: flex;
    justify-content: center;
  }

  .contract_detail_one {
    // margin-right:23px;
    width: 25%;
    line-height: 36px;

    p {
      font-size: 14px;
    }
  }

  .upload {
    display: flex;
    align-items: center;
    padding: 12px;
  }

  .upload_pic {
    display: flex;
    margin-right: 80px;

    .title {
      font-size: 14px;
    }
  }

  .el-loading-mask {
    width: 104px !important;
  }

  .upload-img {
    display: flex;
    align-items: center;
  }

  .el-button--primary {
    color: #FFF;
    background-color: #1890ff;
    border-color: #1890ff;
  }

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin-right: 24px;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 24px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 0;
    justify-content: center;

    span {
      font-size: 12px;
      line-height: 18px;
    }
  }

  .avatar {
    width: 120px;
    height: 120px;
    display: block;
  }

  .addForm {
    padding: 24px 48px;
    background: #fff;
    margin: 12px 0;
    display: flex;
    justify-content: center;
  }

  .addForm2 {
    padding: 24px 48px;
    background: #fff;
    margin: 12px 0;
    display: flex;
    justify-content: left;
  }

  .el-form {
    // display: flex;
    // flex-wrap: wrap;
    // margin-right: 134px;
    &:nth-child(2) {
      margin-right: 0;
    }
  }

  .el-form-item {
    // width: 48%;
    margin-bottom: 14px;

    &:nth-child(2) {
      .el-form-item__content {
        display: flex;
        align-items: center;

        .el-button {
          margin-left: 14px;
        }
      }
    }
  }

  .el-input--mini .el-input__inner {
    height: 30px;
    line-height: 30px;
    // width: 390px;
  }

  .el-form-item__label {
    font-size: 14px;
    color: #333;
  }

  .checkForm {
    background: #fff;

    .checkInfo {
      padding: 24px;
      border-bottom: 1px solid #eaeaea;
      font-size: 16px;
      margin: 0;
    }

    .addCheckInfo {
      padding: 24px;
      text-align: center;
    }
  }
}
</style>


